  .header-content {
    min-height: 650px !important;
  }
}

@media (min-width: 1200px) and (max-width: 1450px) {
  .modern-container {
    left: calc((100% - 1170px)/ 2);
    position: relative;
  }
}

@media (min-width: 1280px) {
  .container {
    max-width: 1280px !important;
  }
  .detail-widgets,
  .sidebar-services {
    margin-left: 40px;
  }
}

/* Hide element on Mobile device */
@media (max-width: 767px) {
